import { select } from "d3-selection";
import intervals from './GTS_2020.json';
import { HierarchyNode, HierarchyRectangularNode, partition, stratify } from "d3-hierarchy";

import type { Selection } from "d3-selection";
import { zoom } from "d3-zoom";
import { transition } from "d3-transition";

type IntervalItem = {
  id: number;
  name: string;
  abbr?: string;
  color: string;
  textColor?: string;
  end: number;
  start: number;
  level?: number;
  parentId?: number;
  leaf?: boolean;
}

interface GeoTimeLineOptions {
  width?: number;
  height?: number;
  tickLength?: number;
  neighborWidth?: number;
  fontSize?: number;
  fontFamily?: string;
  onChange?: (time: number, stage: string[]) => void;
  onAfterChange?: (time: number, stage: string[]) => void;
  intervals?: IntervalItem[];
  margin?: {
    top?: number;
    bottom?: number;
    left?: number;
    right?: number;
  }
}

export default class GeoTimeLine {
  private _options: Required<GeoTimeLineOptions>
  readonly font: string;
  readonly svg: Selection<SVGSVGElement, unknown, HTMLElement, any>;
  readonly hierarchicalData: HierarchyNode<IntervalItem>;
  private _focus: HierarchyRectangularNode<IntervalItem>;
  private _sequence: HierarchyRectangularNode<IntervalItem>[] = [];
  private _width: number;
  private _height: number;
  private _hideSmallTicks: boolean = false
  readonly root: HierarchyRectangularNode<IntervalItem>;
  g: Selection<SVGGElement, unknown, HTMLElement, any>;
  cell: Selection<any, HierarchyRectangularNode<IntervalItem>, SVGGElement, unknown>;
  rect: Selection<SVGRectElement, HierarchyRectangularNode<IntervalItem>, SVGGElement, unknown>;
  private _canvas: HTMLCanvasElement;

  /**
   * Create a GeoTimeLine
   * @param selector CSS selector string
   * @param options 
   */
  constructor(selector: string, options: GeoTimeLineOptions = {}) {
    const opts: Required<GeoTimeLineOptions> = {
      width: 960,
      height: 100,
      tickLength: 10,
      neighborWidth: 25,
      fontSize: 12,
      fontFamily: 'sans-serif',
      onChange: undefined,
      onAfterChange: undefined,
      intervals: intervals,
      margin: {
        top: 20, right: 20, bottom: 30, left: 30,
        ...options.margin
      },
      ...options
    }
    this._width = opts.width
    this._height = opts.height
    this._options = opts

    this.font = `${opts.fontSize}px ${opts.fontFamily}`;

    this.svg = select(selector)
      .append("svg")
      .attr("viewBox", [0, 0, opts.width, opts.height])
      .style("font", this.font);
    
    this.hierarchicalData = stratify<IntervalItem>()(intervals).sum((d) =>
      d.leaf ? d.start - d.end : 0
    );

    const maxLevel = Math.max(...intervals.map(item => item.level ?? 0))
    this._focus = this.root = partition<IntervalItem>()
      .size([opts.width, opts.height * maxLevel - opts.margin.bottom])
      .padding(0)(this.hierarchicalData);
    
    this._canvas = document.createElement('canvas')

    this.init()
  }

  get focus() {
    return this._focus
  }

  get sequence() {
    return this._sequence
  }
  
  init() {
    const { width, height, margin } = this._options

    // zoom available
    this.svg.call(zoom()
      .extent([[0, 0], [width, height]])
      .scaleExtent([1, 8])
      .on("zoom", zoomed));

    function zoomed({ transform }) {
      g.attr("transform", transform);
    }

    // draw cells
    const g = this.svg.append("g");

    const cellGroup = g.append("g").attr("id", "cells");

    const cell = cellGroup
      .selectAll("g")
      .data(this.root.descendants())
      .join("g")
      .attr("transform", (d) => `translate(${d.x0},${d.y0})`);

    const rect = cell
      .append("rect")
      .attr("width", (d) => d.x1 - d.x0)
      .attr("height", (d) => d.y1 - d.y0)
      .attr("fill", (d) => d.data.color)
      .attr("stroke", "white")
      .attr("stroke-width", 0.5)
      .attr("cursor", "grab");

    // add text
    const labelVisible = (d) => +(d.x1 - d.x0 > 14);

    const text = cell
      .append("text")
      .style("user-select", "none")
      .attr("pointer-events", "none")
      .attr("x", (d) => {
        const textX = (d.x1 - d.x0) / 2;
        return Number.isNaN(textX) ? 0 : textX;
      })
      .attr("y", (d) => (d.y1 - d.y0) / 2)
      .attr("fill", (d) => d.data.textColor ?? "black")
      .attr("fill-opacity", labelVisible)
      .attr("text-anchor", "middle")
      .attr("dominant-baseline", "middle")
      .text((d) => {
        const rectWidth = d.x1 - d.x0;
        const labelWidth = this.getTextWidth(d.data.name, this.font);
        const abbrev = d.data.abbr || d.data.name.charAt(0);

        return rectWidth - 10 < labelWidth ? abbrev : d.data.name;
      });
  }

  getTextWidth(text: string, font: string) {
    // re-use canvas object for better performance
    const context = this._canvas.getContext("2d");
    context.font = font;
    const metrics = context.measureText(text);

    return metrics.width;
  }

}

